@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800&display=swap);
body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* general */
@font-face {
  font-family: 'zeldaregular';
  src: url(/margaux/static/media/zelda_free-webfont.45a8d423.woff2) format('woff2'),
       url(/margaux/static/media/zelda_free-webfont.0144796a.woff) format('woff');
  font-weight: normal;
  font-style: normal;
}
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html{
  font-family: 'Poppins', Helvetica, sans-serif;
  font-size: 62.5%;
  height: 100%;
  /* firefox */
  /* scrollbar-color: #fff #212121;
  scrollbar-width: thin; */
  /* color variables */
  --surface-color: #fff;
  --accent-color:#e3cdb3;
  --black:#212121;

  /* transition variables*/
  --transition-duration: 300ms ease;
  overflow: hidden;
}

/* html::-webkit-scrollbar{
  width: 0.5rem;
}
html::-webkit-scrollbar-track{
  background-color: #212121;
}
html::-webkit-scrollbar-thumb{
  background-color: #fff;
  border-radius: 5rem;
  
} */
body{
  height: 100%;
}

/* single components */
ul{
  list-style: none;
}
a{
  color: inherit;
  text-decoration: none;
}

.btn{
  font-size: 1.2rem;
  letter-spacing: 0.1rem;
}
/* layout */
.flex{
  display: flex;
}
.flex.column{
  flex-flow: column;
}

/* hover effects */
.hover-fade,
.text-hover{
  transition: 300ms ease;
  transition: var(--transition-duration);
}

.text-hover{
  transition-delay: 0s !important;
}
.hover-fade:hover{
  opacity: 0.5;
}

.text-hover:hover{
  color:#e3cdb3;
  color:var(--accent-color);
}
@media (min-width:420px) {
  .btn{
    font-size: 1.8rem;
  }
}
@media (min-width:800px){
  .btn{
  }
}
.background{
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgb(11,16,38);
  background: linear-gradient(0deg, rgba(11,16,38,1) 0%, rgba(6,29,55,1) 100%);
  z-index: -24;
}
header{
  padding: 2rem 2.25rem;
  display: flex;
  position: fixed;
  width: 100vw;
  z-index: 24;
}

.logo{
  font-family: 'zeldaregular', sans-serif;
  font-size: 1.6rem;
  letter-spacing: 0.25rem;
  text-decoration: none;
  color: var(--surface-color);
  z-index: 25;
}

.wrapper{
  margin-left: auto;
  z-index: 24;
}

.icons-con{
  display: none;
  align-items: center;
  margin-right: 4.8rem;
}
.icons-con span,.icons-con a{
  display: inline-flex;
}
.icons-con > *+*{
  margin-left: 2.4rem;
}
.icons-con svg{
  fill: var(--surface-color);
}

.hamburger-icon{
  width: 2.5rem;
  height: 1.5rem;
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  cursor: pointer;
}

.hamburger-icon span{
  transition: var(--transition-duration);
}
.hamburger-icon.active span:nth-child(1){
  -webkit-transform: translateY(7px) rotate(45deg);
          transform: translateY(7px) rotate(45deg);
}
.hamburger-icon.active span:nth-child(2){
  -webkit-transform: scale(0);
          transform: scale(0);
}
.hamburger-icon.active span:nth-child(3){
  -webkit-transform: translateY(-7px) rotate(-45deg);
          transform: translateY(-7px) rotate(-45deg);
}

.hamburger-icon span{
  height: 2px;
  width: 100%;
  background-color: var(--surface-color);
}

@media (min-width:420px) {
  header{
    padding: 4rem 4rem 0;
    display: flex;
  }
  .logo{
    font-size: 2.4rem;
  }
  .icons-con{
    display: flex;
  }

  .hamburger-icon{
    width: 5rem;
    height: 2.4rem;
  }
  .hamburger-icon span{
    height: 3px;
  }
  .hamburger-icon.active span:nth-child(1){
    -webkit-transform: translateY(11px) rotate(45deg);
            transform: translateY(11px) rotate(45deg);
  }
  .hamburger-icon.active span:nth-child(3){
    -webkit-transform: translateY(-11px) rotate(-45deg);
            transform: translateY(-11px) rotate(-45deg);
  }
}

@media (min-width:800px){
  .icons-con{
    margin-right: 6.4rem;
  }
  .icons-con > *+*{
    margin-left: 3.2rem;
  }
}
.global-nav{
  position: fixed;
  top: 0;
  left: 0;
  height: 0;
  width: 100vw;
  transition: height var(--transition-duration);
  background: rgb(6,29,55);
  background: linear-gradient(270deg, rgba(6,29,55,1) 0%, rgba(16,40,73,1) 20%, rgba(53,71,95,1) 40%, rgba(36,43,75,1) 60%, rgba(22,27,54,1) 80%, rgba(11,16,38,1) 100%);
}
.global-nav.active{
  height: 100vh;
}

.global-nav ul{
  height: 100%;
  justify-content: center
}
.global-nav li{
  display: flex;
  justify-content: center;
}

.global-nav li+li{
  margin-top: 2.4rem;
}

.global-nav li:last-child > *+*{
  margin-left: 2.8rem;
}

.global-nav a{
  transition: opacity var(--transition-duration),
   color var(--transition-duration);
  font-size: 3.8rem;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  color: var(--surface-color);
  visibility: hidden;
  opacity: 0;
  display: none;
}
.global-nav svg{
  fill: var(--surface-color);
}

.global-nav.active a{
  display: block;
  visibility: visible;
  opacity: 1;
  transition-delay: 0.3s, 0s!important;
}

@media (min-width:420px) {
  .global-nav li:last-child{
    display: none;
  }
  .global-nav a{
    font-size: 6.4rem;
  }
}

@media (min-width:800px){
  .global-nav a{
    font-size: 10rem;
  }
}
main{
  height: 100vh;
  -ms-scroll-snap-type: y mandatory;
      scroll-snap-type: y mandatory;
  overflow: auto;
  scroll-behavior: smooth;
  /* firefox */
  scrollbar-color: #fff #212121;
  scrollbar-width: thin;
}
main::-webkit-scrollbar{
  width: 0.5rem;
}
main::-webkit-scrollbar-track{
  background-color: #212121;
}
main::-webkit-scrollbar-thumb{
  background-color: #fff;
  border-radius: 5rem;
}
section{
  scroll-snap-align: start;
  height: 100vh;
  display: flex;
  align-items: center;
  position: relative;
}
section:first-child .section-header__title{
  text-transform: uppercase;
}
.section-header{
  max-width: 60%;
  align-self: flex-end;
  margin-bottom: 8rem;
  margin-left: 6.25%;
  margin-right: 6.25%;
  color: var(--surface-color);
  transition: var(--transition-duration);
  position: absolute;
  z-index: 22;
}
.section-header *{
  transition: var(--transition-duration);
}
.section-header >*+*, 
.section-header__lead > *+*, 
.linklist > *+*{
  margin-top: 0.8rem;
}
.section-header svg{
  fill: var(--surface-color);
}
.section-header__title{
  font-size: 3.8rem;
  text-transform: capitalize;
  line-height: 1;
  font-weight: 600;
  letter-spacing: 0.2rem;
  text-shadow: 0 10px 30px rgba(2, 11, 22, 0.5);
}

.section-header__lines span{
  display: block;
  height: 0.2rem;
  width: 4.4rem;
  border-radius: 5rem;
  background-color: var(--accent-color);
}

.section-header__lines span:last-child{
  margin-top: 0.8rem;
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
}

section > a{
  position: relative;
  right: 0;
  margin: 0 auto;
  width: 80%;
  max-width: 102rem;
  min-width: 28.8rem;
  height: 55.63%;
  box-shadow: 0 80px 140px -40px rgba(0, 0, 0, 0.6);
}
section > a img{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  -webkit-filter: brightness(0.95);
          filter: brightness(0.95);
}
/* section > a::before{
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  
} */
.section-header__lead p,
.section-header__lead > ul.linklist{
  font-size: 1.2rem;
  letter-spacing: 0.1rem;
  text-shadow: 0 10px 30px rgba(2, 11, 22, 0.5);
}
.section-header__lead > ul{
  display: none;
}
.section-header__lead > ul.linklist{
  display: block;
}
.section-header__lead > a{
  display: none;
}
.section-header__lead > a:focus,
.section-header__lead > a:hover{
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
}
.section-header__lead > a:active{
  -webkit-filter: brightness(1.2);
          filter: brightness(1.2);
  color: #212121;
}
.section-header__lead > a.link{
  display: inline-block;
  padding: 1.2rem 2.4rem;
  background-color: var(--accent-color);
  border-radius: 5rem;
  text-shadow: 0 3px 10px rgba(2, 11, 22, 0.5);
}

.section-header__lead li a+a{
  margin-left: 1.8rem;
}
.section-header__lead svg{
  fill: var(--surface-color);
}

@media (min-width:420px) {
  .section-header >*+*, 
  .section-header__lead > *+*, 
  .linklist > *+*{
    margin-top: 1.6rem;
  }
  .section-header{
    margin-left: 10%;
    margin-right: 0;
  }
  .section-header svg:focus,
  .section-header svg:hover{
    -webkit-transform: translateX(-4px);
            transform: translateX(-4px);
  }
  .section-header__title{
    font-size: 5.8rem;
  }
  .section-header__lines span{
    height: 0.4rem;
    width: 7rem;
  }
  .section-header__lead p,
  .section-header__lead > ul.linklist{
    font-size: 2.4rem;
  }
  section:first-child >a{
    width: 30.25%;
    margin-right: 16%;
    -webkit-filter: brightness(0.9);
            filter: brightness(0.9);
  }
  section > a{
    margin-right: 8%;
    margin-left: auto;
    width: 53.25%;
    height: 74.8%;
  }
}

@media (min-width:800px){
  .section-header{
    margin-top: 0;
    margin-bottom: 0;
    align-self: center;
  }
  .section-header >*+*,
  .section-header__lead > *+*,
  .linklist > *+*{
    margin-top: 3.2rem;
  }
  .section-header__title{
    font-size: 12rem;
  }
  .section-header__lines span{
    height: 0.4rem;
    width: 8.8rem;
  }
  .section-header__lead p,
  .section-header__lead > ul.linklist{
    font-size: 2.8rem;
  }

  .section-header__lead > a.link{
    padding: 1.4rem 3.2rem;
  }
}
.aboutPage+.aboutPage{
  background-color: var(--accent-color);
  padding: 4.8rem 2.4rem;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
}
/* overides */
.aboutPage > a{
  height: 74.8%;;
  width: 100vw !important;
  max-width: 115.4rem !important;
  margin: 0 !important;
  margin-left: 0 !important;
  margin-bottom: auto !important;
}
.aboutPage .section-header a > svg{
  width: 2.4rem;
  height: 2.4rem;
}
.aboutPage:nth-child(2) > a, .aboutPage:nth-child(2) img{
  border-radius: 100%;
  min-width: 32rem!important;
  max-width: 32rem!important;
  width: auto !important;
  width: initial !important;
  height: 32rem!important;
  
}
.aboutPage:nth-child(2){
  flex-flow: column-reverse;
}
.aboutPage:nth-child(2) > a{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32rem!important;
  margin-bottom: 2.4rem !important;
}
.about-section-header__title,.about-section-header__lead{
  color: var(--black);
}
.about-section-header__title{
  font-size: 1.8rem;
  margin-bottom: 1.8rem;
}
.about-section-header__lead{
  font-size: 1.2rem;
}

footer{
  height: 35vh;
  scroll-snap-align: end;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--surface-color);
}
footer svg{
  fill: var(--surface-color);
}
footer ul li{
  display: flex;
  justify-content: center;
  font-size: 1.4rem;
}
footer ul li + li{
  margin-top: 1.8rem;
}
footer ul li > *+*{
  margin-left: 2.4rem;
}



/* specific to page */


@media (min-width:420px){
  .aboutPage .section-header a > svg{
    width: 4.8rem;
    height: 4.8rem;
  }
  .about-section-header__title{
    font-size: 2.4rem;
  }
  .about-section-header__lead{
    font-size: 1.4rem;
  }
}

@media (min-width:800px){
  .aboutPage:nth-child(2){
    padding: 0 10%;
    width: 100%;
    margin: 0 auto;
    flex-flow: row;
  }
  .aboutPage > a{
    height: 100%;
    width: 60.67% !important;
    margin-left: auto!important;
    margin-right: 8%!important;
    margin-bottom: 0!important;
  }
  .aboutPage .section-header a > svg{
    width: 6.4rem;
    height: 6.4rem;
  }
  .about-section-header__title{
    font-size: 4rem;
  }
  .about-section-header__lead{
    font-size: 1.8rem;
  }
  footer ul li{
    font-size: 2.8rem;
  }
  footer ul li + li{
    margin-top: 2.4rem;
  }
  footer ul li > *+*{
    margin-left: 3.6rem;
  }
}
@media (min-width:1600px){
  .aboutPage:nth-child(2){
    padding: 0 15%;
  }
}
